<style>
  .icon-add-wrap-i {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .memeber-mesg-item {
    margin-right: 20px;
    color: #222222;
  }
  .memeber-mesg-item>span:nth-child(1) {
    font-weight: bold;
  }
</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top" @submit.native.prevent>        
        <el-form-item label="会员手机号">
          <el-input class="search-input" v-model.trim="searchContent.phone" placeholder="会员手机号" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="优惠券状态">
          <el-select class="search-input" v-model="searchContent.status" filterable placeholder="请选择优惠券状态" size="small" @change="doSearchHandel(true)" clearable>
            <el-option
              v-for="item in statusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini" :disabled="!searchContent.phone">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    <hr class="s-slip">
    <div class="handel-wraper icon-add-wrap-i">
      <div class="memeber-mesg-item">
        <span>会员手机号:</span>
        <span>{{memeberMesg.phone}}</span>
      </div>
      <div class="memeber-mesg-item">
        <span>会员昵称:</span>
        <span>{{memeberMesg.name}}</span>
      </div>
      <div class="memeber-mesg-item">
        <span>会员归属城市:</span>
        <span>{{memeberMesg.city}}</span>
      </div>
      <div class="memeber-mesg-item">
        <span>会员归属门店:</span>
        <span>{{memeberMesg.vestStoreNo}}</span>
      </div>
    </div>
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="80px" prop="promId" label="优惠券id"></el-table-column>
        <el-table-column min-width="120px" prop="title" label="优惠券名称"></el-table-column>
        <el-table-column min-width="120px" prop="ruleSource" label="优惠券使用范围"></el-table-column>
        <el-table-column min-width="90px" prop="promType" label="优惠券类型"></el-table-column>
        <el-table-column min-width="160px" prop="promDetail" label="优惠券详情"></el-table-column>
        <el-table-column min-width="120px" label="优惠券有效期">
          <template slot-scope="scope" size="small">
            <div>
              <div>
                {{scope.row.beginTime}}
              </div>
              <div>
                至 
              </div>
              <div>
                {{scope.row.endTime}}
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="160px" prop="createTime" label="优惠券发送时间"></el-table-column>
        <el-table-column min-width="100px" prop="statusStr" label="优惠券状态"></el-table-column>
        <el-table-column min-width="110px" prop="city" label="活动归属城市"></el-table-column>
        <el-table-column min-width="100px" label="所属活动id" fixed="right">
          <template slot-scope="scope" size="small">
            <div>
              <router-link :to="scope.row.redirectUrl" target="_blank">{{scope.row.promCreateId}}</router-link>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="100px" label="操作" fixed="right">
          <template slot-scope="scope" size="small">
            <el-button v-if="scope.row.status == 0" type="text" size="mini" @click="invalidate(scope.row)">优惠券作废</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[20, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  const reqUrl = {
    // 优惠券查询列表
    promList: '/member/erp/mermberProm/promList',
    // 优惠券作废
    updateStatus: '/member/erp/memberProm/updateStatus',
  }
  export default {
    data() {
      return {
        loading: false,
        pageNum: 1,
        pageSize: 20,
        total: 0,
        searchContent: {
          // 会员手机号
          phone: '',
          status: ''
        },
        tableData: [],
        memeberMesg: {
          city: '-', // 会员归属城市
          vestStoreNo: '-', // 会员归属门店
          name: '-', // 会员昵称
          phone: '-' // 会员手机号
        },
        statusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '已失效',
            value: -1
          },
          {
            label: '未使用',
            value: 0
          },
          {
            label: '已过期',
            value: 1
          },
          {
            label: '已使用',
            value: 2
          }
        ],
      }
    },
    methods: {
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 获取订单列表内容]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        if(!this.searchContent.phone) {
          this.$notify.warning({
            title: '温馨提示',
            message: '会员手机号不能为空'
          })
          return
        }

        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(reqUrl.promList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data) {
              this.memeberMesg.phone = res.data.phone || '-'
              this.memeberMesg.vestStoreNo = res.data.vestStoreNo || '-'
              this.memeberMesg.name = res.data.name || '-'
              this.memeberMesg.city = res.data.city || '-'

              if(res.data.pageVo && res.data.pageVo.result && res.data.pageVo.result.length) {
                for(let item of res.data.pageVo.result) {
                  item.redirectUrl = `/discount_coupon/showcp/${item.promCreateId}`
                }
                this.tableData = res.data.pageVo.result
                this.total = res.data.pageVo.total
              }else {
                this.tableData = []
                this.total = 0
              }
            }else {
              this.memeberMesg.phone = '-'
              this.memeberMesg.vestStoreNo = '-'
              this.memeberMesg.name = '-'
              this.memeberMesg.city = '-'
              this.tableData = []
              this.total = 0
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      // 优惠券作废
      invalidate(row) {
        let _params = {
          mpId: row.mpId,
          status: -1
        }
        this.$http.post(reqUrl.updateStatus, {..._params}).then(res => {
          if(res.code === 0) {
            this.$message.success("优惠券作废成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    }
    
  }
</script>


