<style>
  .common-tab .el-tabs__header {
    margin-bottom: 0
  }
  .common-tab .el-tabs__header .el-tabs__item.is-active{
    background: #fff;
  }
</style>

<template>
  <div>
    <el-tabs type="card" v-model="activeName" class="common-tab">
      <el-tab-pane label="会员查询" name="first">
        <member-search ref="memberSearch"></member-search>
      </el-tab-pane>
      <el-tab-pane label="活动查询" name="second">
        <activity-search ref="activitySearch"></activity-search>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import memberSearch from './memberSearch'
  import activitySearch from './activitySearch'

  export default {
    data() {
      return {
        activeName: 'first',
      }
    },
    components: {
      'member-search': memberSearch,
      'activity-search': activitySearch
    },
    created() {
      this.activeName = this.$route.params.id ? 'second':'first'
    },
    methods: {
    }
  }
</script>


