<style>
</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top" @submit.native.prevent>        
        <el-form-item label="优惠券活动编码">
          <el-input class="search-input" v-model.trim="searchContent.recordId" placeholder="请输入活动编码" size="small" @keyup.enter.native="doSearchHandel()" clearable></el-input>
        </el-form-item>
        <!-- <el-form-item label="状态">
          <el-select class="search-input" v-model="searchContent.status" filterable placeholder="请选择优惠券状态" size="small" @change="doSearchHandel()" clearable>
            <el-option
              v-for="item in statusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item> -->

        <el-form-item>
          <el-button type="primary" @click="doSearchHandel()" size="mini" :disabled="!searchContent.recordId">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="110px" prop="triggerType" label="活动类型" :formatter="triggerTypeFormater"></el-table-column>
        <el-table-column min-width="80px" prop="id" label="活动编码"></el-table-column>
        <el-table-column min-width="120px" prop="title" label="活动名称"></el-table-column>
        <el-table-column min-width="120px" label="活动时间">
          <template slot-scope="scope" size="small">
            <div>
              <div>
                {{scope.row.beginTime.slice(0, 10)}}
              </div>
              <div>
                至 
              </div>
              <div>
                {{scope.row.endTime.slice(0, 10)}}
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="80px" prop="cityName" label="参与区域"></el-table-column>
        <el-table-column min-width="120px" label="优惠券有效期">
          <template slot-scope="scope" size="small">
            <div v-if="scope.row.useDayCount">
              {{scope.row.useDayCount}} 天
            </div>
            <div v-else-if="scope.row.useBeginTime && scope.row.useEndTime">
              <div>
                {{scope.row.useBeginTime.slice(0, 10)}}
              </div>
              <div>
                至 
              </div>
              <div>
                {{scope.row.useEndTime.slice(0, 10)}}
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="110px" prop="totalCount" label="优惠券总张数"></el-table-column>
        <el-table-column min-width="80px" prop="statusStr" label="活动状态"></el-table-column>
        <el-table-column min-width="80px" prop="sendMemberCount" label="发放人数"></el-table-column>
        <el-table-column min-width="80px" prop="sendMemberPromCount" label="发放张数"></el-table-column>
        <el-table-column min-width="90px" prop="usedPromCount" label="已使用张数"></el-table-column>
        <el-table-column min-width="90px" prop="unUsedPromCount" label="未使用张数"></el-table-column>
        <el-table-column min-width="90px" prop="overPromCount" label="已过期张数"></el-table-column>
        <el-table-column min-width="90px" prop="losePromCount" label="已失效张数"></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  const reqUrl = {
    // 活动优惠券查询
    promCountTotal: '/member/erp/memberProm/promCountTotal',
  }
  export default {
    data() {
      return {
        loading: false,
        searchContent: {
          // 优惠券活动ID
          recordId: '',
          // status: ''
        },
        tableData: [],
        // statusOptions: [
        //   {
        //     label: '全部',
        //     value: ''
        //   },
        //   {
        //     label: '已失效',
        //     value: -1
        //   },
        //   {
        //     label: '未使用',
        //     value: 0
        //   },
        //   {
        //     label: '已过期',
        //     value: 1
        //   },
        //   {
        //     label: '已使用',
        //     value: 2
        //   }
        // ],
      }
    },
    created() {
      this.searchContent.recordId = this.$route.params.id
      this.doSearchHandel()
    },
    methods: {
    /**
     * [triggerTypeFormater 活动格式化]
     * @param  {[type]} row       [description]
     * @param  {[type]} column    [description]
     * @param  {[type]} cellValue [description]
     * @return {[type]}           [description]
     */
    triggerTypeFormater(row, column, cellValue) {
      // 活动类型，触发条件 1:活动-发放;2:分享;3:签到;4:充值（单次发放）;6:领取；71.消费；42.充值(多次发放),12：活动-指定用户发券  81 扫码送券  82 扫码消费送券  111 天降红包
      switch(cellValue) {
        case 1:
          return '直接发放'
        break;
        case 4:
          return '充值返券(单次)'
        break;
        case 42:
          return '充值返券(多次)'
        break;
        case 71:
          return '消费发放'
        break;
        case 2:
          return '分享返券'
        break;
        case 3:
          return '签到返券'
        break;
        case 6:
          return '领取活动'
        break;
        case 81:
          return '扫码送券'
        break;
        case 82:
          return '扫码消费送券'
        break;
        case 91:
          return '省钱礼包'
        break;
        case 12:
          return '指定客户'
        break;
        case 111:
          return '天降红包'
        break;
        case 121:
          return '积分换券'
        break;
        default:
          return '其它券'
        break;
      }
    },
      /**
       * [doSearchHandel 获取订单列表内容]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel() {
        if(!this.searchContent.recordId) {
          return
        }
        this.loading = true
        this.$http.post(reqUrl.promCountTotal, { ...this.searchContent}).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data) {
              if(res.data && res.data.length) {
                this.tableData = res.data
              }else {
                this.tableData = []
              }
            }else {
              this.tableData = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      // 优惠券作废
      invalidate(row) {
        let _params = {
          mpId: row.mpId,
          status: row.status
        }
        this.$http.post(reqUrl.updateStatus, {..._params}).then(res => {
          if(res.code === 0) {
            this.$message.success("优惠券作废成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    }
    
  }
</script>


